/* eslint-disable */
<template>
  <div>
    <core-toolbarinstitution />
    <v-container
      fill-height
      fluid
      grid-list-xl
    >
      <v-layout
        justify-center
        wrap
      >
        <material-card
          color="blue"
          title="Current Profile"
        >
          <v-form>
            <v-container py-0>
              <v-layout wrap>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="name"
                    disabled
                    type="string"
                    label="University Name"
                  />
                </v-flex>

                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="id"
                    disabled
                    label="University Id"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="email"
                    label="Email"
                    disabled
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="password"
                    disabled
                    label="Password"
                    :type="'password'"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md8
                >
                  <v-text-field
                    v-model="location.address"
                    disabled
                    label="Address"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="location.city"
                    disabled
                    label="City"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="location.country"
                    disabled
                    label="Country"
                  />
                </v-flex>
                <v-flex
                  xs12
                  md4
                >
                  <v-text-field
                    v-model="postalCode"
                    disabled
                    label="Postal Code"
                  />
                </v-flex>
                <v-flex
                  xs12
                  text-xs-right
                >
                  <v-btn
                    class="mx-0 font-weight-light"
                    color="blue"
                    href="/institutionloggedin/editInformation"
                  >
                    Edit
                  </v-btn>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
        </material-card>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
const axios = require('axios')

export default {
  name: 'InstitutionLoggedIn',
  data () {
    return {
      id: '',
      name: '',
      phoneNumber: '',
      email: '',
      password: '',
      location : {
        city: '',
        country: '',
        address: ''
      },
    }
  },
  mounted () {
    const axiosConfig = {
      headers: {
        'Content-Type': 'application/json',
        'Authorization': 'Bearer ' + this.$store.state.token
      }
    };
    // console.log(this.$store.state.token);
    axios.get('http://localhost:8080/institutions', axiosConfig).then(resp => {
      console.log(resp)
      var institutionData = resp.data;
      this.name = institutionData.name;
      this.id = institutionData.id;
      this.email = institutionData.email;
      this.phoneNumber = institutionData.phoneNumber;
      this.password = institutionData.password;
      this.location.city = institutionData.location.city;
      this.location.country = institutionData.location.country;
      this.location.address = institutionData.location.address;
    }).catch(error => {
      console.log(error);
    })
  }
}
</script>
